.mario-chat {
  max-width: 600px;
  margin: 30px auto;
  border: 1px solid #ddd;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.05);
}

.chat {
  background-color: white;
  width: 300px;
  position: absolute;
  height: 477px;
  left: -324px;
  bottom: 1px;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.78);
  border-radius: 30px;

  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 100;
  font-family: "Lato";
}

.screen-window {
  background-color: black;
  width: 274px;
  height: 148px;
  // border-radius: 9;
}

.output p {
  padding: 14px 0px;
  margin: 0 20px;
  border-bottom: 1px solid #e9e9e9;
  color: #555;
}

.feedback p {
  color: #aaa;
  padding: 14px 0px;
  margin: 0 20px;
}

.output strong {
  color: #575ed8;
}

.input-chat {
  padding: 10px 20px;
  box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #eee;
  font-family: Nunito;
  font-size: 16px;
}

.button-chat {
  color: #fff;
  font-size: 18px;
  border: 0;
  padding: 12px 0;
  width: 100%;
}

.mail {
  margin: 0 !important;
  padding: 0 !important;
}

.ui.green.button,
.ui.green.buttons .button {
  background-color: #7ca5b2 !important;
  color: #fff;
  font-family: "Lato";
  text-shadow: none;
  background-image: none;
}

.ui.button {
  background-color: #7ca5b2 !important;
}

.ui.green.button:hover {
  background-color: #5b8595 !important;
  color: #fff;
  text-shadow: none;
  background-image: none;
}

.container-ChatCmp {
  display: flex;
  justify-content: center;
}
.header-chat {
  width: 100%;
  height: 51px;
  background: linear-gradient(rgb(82, 131, 146) 0%, rgba(124, 165, 178, 1) 100%);
  border-radius: 30px 30px 0 0px;
  margin-top: -1px;
}
.window-chat {
  height: 262px;
  width: 100%;
  overflow-y: scroll;
}
.textArea-chat {
  width: 298px !important;
  height: 91px !important;
  border-right: none !important;
  border-top: none !important;
  border-left: none !important;
  border-bottom: none !important;
  resize: none !important;
  background-color: #8080801c !important;
}

.chat-button {
  display: flex !important;
  flex-direction: column !important;
  justify-content: center !important;
  align-items: flex-end !important;
  margin-right: 30px !important;
  margin-top: 8px !important;
}

.form-chat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.transition.fly {
  animation-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  height: 470px;
}

.send-message-details{
  background-color: #3b5998;
  color: #fff;
  text-shadow: none;
  font-size: 1rem;
  background-image: none;
  box-shadow: 0 0 0 0 rgba(34,36,38,.15) inset;
  cursor: pointer;
  display: inline-block;
  min-height: 1em;
  outline: 0;
  border: none;
  vertical-align: baseline;
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0 .25em 0 0;
  padding: 10px;
  text-transform: none;
  text-shadow: none;
  font-weight: 700;
  line-height: 1em;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  border-radius: .28571429rem;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34,36,38,.15) inset;
  -webkit-user-select: none;
  user-select: none;
  transition: opacity .1s ease,background-color .1s ease,color .1s ease,box-shadow .1s ease,background .1s ease;
  will-change: '';
  -webkit-tap-highlight-color: transparent;
}

