/* CALENDAR */
.calendar-container {
  display: flex;
  flex-direction: column;
  padding: 100px;
}

@media  (max-width: 805px){
  .calendar-container {
    display: none;
  }
}

.calendar-content {
  border: 1px solid;
  border-radius: 12px;
  border-color: $clr2;
  padding: 23px;
  padding-bottom: 5px;
  padding-top: 5px;
  position: sticky;
  top: 80px;
  margin-left: 30px;
}



/* GUIDE DETAILS */

.guide-header {
  font-family: "Lato-Bold";
  font-size: rem(50px);
  color: $clr2;
  padding: 0;
  margin-top: 30px;
}

.guide-tags{
  border-bottom: 1px solid;
  border-color: $clr2;
  display: flex;
}

.lang-from-guide{
  font-weight: bold;
  color: $clr2;
  line-height: 1.8;
  border-radius: 15px;
  display: flex;
  margin-right: 10px;
}


.from-guide{
  text-transform: capitalize;
  // width: 70%;
  color: $clr2;
  font-family: "Lato";
  font-weight: normal;
  font-size: rem(20px);
}

.guide-field-title{
  font-weight: bold;
  color: $clr2;
}

.guide-lang{
  justify-content: flex-end;
  flex-direction: row;
  // width: 292px;
  color: $clr2;
  font-weight: normal;
  font-size: rem(20px);
}

.guide-price{
  text-align: center;
  color: $clr2;
}

.guide-rank{
  color: $clr2;
}

.guide-short-desc > * {
  font-family: "Lato";
  font-weight: normal;
  font-size: rem(25px);
  line-height: 35px;
  color: $clr2;
  padding: 0;
  word-break: break-word;
  // width: 567px;
}

.guide-desc {
font-family: "Lato";
font-size: rem(20px);
line-height: 30px;
color: $clr2;
padding: 0;
word-break: break-word;
// width: 567px;
border-bottom: 1px solid;
}

.guide-reviews-title {
  font-family: "Lato";
  font-size: rem(20px);
  color: $clr2;
  margin-right: 10%;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 0;
}


.guide-details {
  margin: 70px auto;
  max-width: 1100px;
  padding: 0 10px;
}


