.guide-review-title{
  font-family: "Lato";
  font-size: rem(27px);
  line-height: 45px;
  color: $clr2;
  padding: 0;
  word-break: break-word;
  // width: 567px;
}

.guide-header > * {
  font-size: 7.75rem;
  color: $clr2;
}
.guide-img-details {
  // width: 500px;
  color: $clr2;
}
.guide-img-details > * {
  // width: 68%;
  border-radius: 16px;
}

.price-rank{
  display: flex;
  justify-content: center;  
  font-family: "Lato-Bold";
  font-size: rem(22px);
  line-height: 45px;
  color: $clr2;
  padding: 0;
}

.num-of-people {
  display: flex;
  justify-content: center;
  margin-top: 12px;
  margin-bottom: 10px;
  color: $clr2;
}

.calendar-title {
  display: flex;
  justify-content: center;
  font-size: rem(50px);
  margin-bottom: 5px;
  color: $clr2;
}

.calendar-title > * {
  font-size: 35px;
}

.num-of-people > * {
  color: $clr2;
  font-size: 23px;
}



/* BUTTONS */
.back-btn {
  font-family: "Lato";
  text-align: center;
  font-size: 15px;
  border-radius: 3px;
  width: 70px;
  padding: 10px;
  background-color: white;
  border: 1px solid $clr3;
  color: #da5114;
  cursor: pointer;
  opacity: 1;
  transition: 0.3s;
  margin-top: 10px;
  outline: 0;
}

.back-btn:hover{
  background-color: $clr3;
  color: white;
}

.container-book {
  margin-top: 140px;
}

.book-btn {
  font-family: "Lato";
  border: none;
  text-align: center;
  font-size: 16px;
  width: 70px;
  height: 38px;
  background-color: #ef8758;
  border-radius: 0 9px 9px 0;
  color: white;
  cursor: pointer;
  margin-right: 7px;
  opacity: 1;
  transition: 0.3s;
}

.book-input{
  padding-left: 20px;
  width: 100px;
  border-right-style: none;
}

.book-btn:hover {
  opacity: 0.8;
}


.book-btn:focus{
  outline: none;
}

.book-input{
  outline: none;
  border: 1px solid $clr3;
  border-right-style:none;
  border-radius: 9px 0 0 9px;
}

.book-input:focus{
  border: 1px solid $clr4;
  border-right-style:none;
  outline: none;
}

.num-of-guests {
  border-radius: 9px 0 0 9px;
  width: 180px;
  height: 38px;
  margin-bottom: 15px;
  border: 1px solid;
  border-color: rgb(220, 223, 230);
  outline: none;
}


/* CONTAINERS */
.review-container {
  // width: 75%;
  height: auto;
  color: $clr2;
  text-align: inherit;
  font-size: 3px;
  word-wrap: break-word;
  border-bottom: 0.5px solid;
}

.num-of-reviews{
  margin-top: 20px;
  border-top:1px solid;
}

.book-btn-container {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

.context-container > * {
  padding-bottom: 20px;
  margin-bottom: 10px;
}
.btn-container {
  padding: 0;
  outline: none;
}

.guide-activites{
  display: flex;
  width: 50%;
  color: $clr2;
  img{
    padding-top: 20px;
    padding-right: 8px;
    margin: 3px;
  }
}

@media screen and (max-width: 950px){
  .guide-activites {
    width: 100%;
    flex-direction: column;
  }
  }

.divider{
  padding-top: 15px;
  border-bottom: 1px solid;
  // width: 575px;
}

  